import React, { useState } from 'react';

const OrderTable = ({ order }) => {
  const [status, setStatus] = useState(null)

  const handleBlur = (event) => {
    setStatus(event.target.value)
  }
  const updateStatus = (id) => {
    const orderStatus = { status: status }
    console.log(id, status);
    fetch(`https://mighty-badlands-04385.herokuapp.com/update/${id}`, {
      method: "PATCH",
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
      body: JSON.stringify(orderStatus)
    })
      .then(res => res.json())
      .then(data => console.log("updated"))

  }
  // console.log(order);
  return (
    <div className="px-3 py-2">
      <div className="d-flex justify-content-between">
        <h6 style={{ width: "150px" }}>{order.name}</h6>
        <h6 style={{ width: "220px" }}>{order.email}</h6>
        <h6 style={{ width: "150px" }}>{order.title}</h6>
        <h6>{order.price}</h6>
        <div className="d-flex">
          <select onBlur={handleBlur} className="custom-select mr-sm-2" id="inlineFormCustomSelect">
            <option defaultValue>Status</option>
            <option value="On Going">On Going</option>
            <option value="Pending">Pending</option>
            <option value="Done">Done</option>
          </select>
          <button onClick={() => updateStatus(order._id)} className="btn btn-sm btn-primary">Update</button>
        </div>
      </div>
    </div>
  );
};

export default OrderTable;