import React from 'react';
import "./ReviewCard.css";
const ReviewCard = ({ review }) => {
  const { image, name, company } = review
  return (
    <div className=" col-md-5 m-auto">
      <div className="d-flex mt-5">
        <img style={{ width: "80px", borderRadius: "50%" }} src={image} alt="" />
        <div style={{ width: "160px", padding: "10px" }}>
          <h5 className="mb-0">{name}</h5>
          <small className="text-success">{company}</small>
        </div>
      </div>
      <div>
        <q className="client-review bg-dark mt-3">{review.description}</q>
      </div>
    </div>
  );
};

export default ReviewCard;