import React, { useContext, useEffect, useState } from 'react';
import { UserContext } from '../../../App';
import Sidebar from '../../Shared/Sidebar/Sidebar';
import BookingDetail from './BookingDetail';

const BookingList = () => {
  const [loggedInUser, setLoggedInUser] = useContext(UserContext)
  const [orders, setOrders] = useState([]);
  console.log(orders);

  useEffect(() => {
    fetch("https://mighty-badlands-04385.herokuapp.com/orders?email=" + loggedInUser.email)
      .then(res => res.json())
      .then(data => setOrders(data))
  }, [loggedInUser.email])

  return (
    <div className="row">
      <div className="col-md-2">
        <Sidebar></Sidebar>
      </div>
      <div className="col-md-10">
        <h3 className="text-center text-primary mb-5 mt-3">Your Bookings</h3>
        {
          orders.map(order => <BookingDetail key={order._id} order={order}></BookingDetail>)
        }
      </div>
    </div>
  );
};

export default BookingList;